import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

import { UserService, AuthenticationService, AlertService } from '../_services';

@Component({ templateUrl: 'tracker-form.component.html',
styleUrls: ['./tracker-form.component.css'] })
export class TrackerFormComponent implements OnInit {
    submitTFA: FormGroup;
    loading = false;
    submitted = false;

    constructor(
        private formBuilder: FormBuilder,
        private router: Router,
        private authenticationService: AuthenticationService,
        private userService: UserService,
        private alertService: AlertService
    ) {
        // redirect to home if already logged in
        // if (this.authenticationService.currentUserValue) {
        //     this.router.navigate(['/']);
        // }
    }

    ngOnInit() {
        // set up validators in form

        // this.submitTFA = this.formBuilder.group({
        //     firstName: ['', Validators.required],
        //     lastName: ['', Validators.required],
        //     username: ['', Validators.required],
        //     password: ['', [Validators.required, Validators.minLength(6)]]
        // });
    }

    // convenience getter for easy access to form fields
    get f() { return this.submitTFA.controls; }

    onSubmit() {
        this.submitted = true;

        this.alertService.clear();

        // stop here if form is invalid
        if (this.submitTFA.invalid) {
            return;
        }

        this.loading = true;
        // will need another service that will create the TFA form
        this.userService.register(this.submitTFA.value)
        // figure out what pipe means here
            // .pipe(first())
            .subscribe(
                data => {
                    this.alertService.success('Successful submission', true);
                    this.router.navigate(['/home'], { queryParams: { registered: true }});
                },
                error => {
                    this.alertService.error(error);
                    this.loading = false;
                });
    }
}
